'use client';

import React, { useState, useEffect, useRef, CSSProperties } from 'react';
import Image from 'next/image';

const posters = [
  { id: 1, src: '/images/poster1.jpg', alt: 'Poster 1' },
  { id: 2, src: '/images/poster2.jpg', alt: 'Poster 2' },
  { id: 3, src: '/images/poster3.jpg', alt: 'Poster 3' },
  { id: 4, src: '/images/poster4.jpg', alt: 'Poster 4' },
  { id: 5, src: '/images/poster5.jpg', alt: 'Poster 5' },
  { id: 6, src: '/images/poster6.jpg', alt: 'Poster 6' },
  { id: 7, src: '/images/poster7.jpg', alt: 'Poster 7' },
  { id: 8, src: '/images/poster8.jpg', alt: 'Poster 8' },
  { id: 9, src: '/images/poster9.jpg', alt: 'Poster 9' },
  { id: 10, src: '/images/poster10.jpg', alt: 'Poster 10' },
  { id: 11, src: '/images/poster11.jpg', alt: 'Poster 11' },
  { id: 12, src: '/images/poster12.jpg', alt: 'Poster 12' },
  { id: 13, src: '/images/poster13.jpg', alt: 'Poster 13' },
  { id: 14, src: '/images/poster14.jpg', alt: 'Poster 14' },
  { id: 15, src: '/images/poster15.jpg', alt: 'Poster 15' },
];

export const BlackBackgroundCarousel = () => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);

  const nextSlide = () => setCurrentIndex(prev => (prev + 1) % posters.length);
  const prevSlide = () => setCurrentIndex(prev => (prev - 1 + posters.length) % posters.length);
  const goToSlide = (index: number) => setCurrentIndex(index);

  const visiblePostersInArc = 9;
  const halfVisible = Math.floor(visiblePostersInArc / 2);

  const getTransform = (index: number) => {
    let diff = index - currentIndex;
    if (diff > posters.length / 2) diff -= posters.length;
    if (diff < -posters.length / 2) diff += posters.length;

    const angle = (100 / (visiblePostersInArc - 1)) * diff;
    const zIndex = index === currentIndex ? 10 : 5;
    const scale = 0.6; 
    const translateZ =650; 
    
    const isVisible = Math.abs(diff) <= halfVisible;

    // Use type assertion for pointerEvents to fix the TypeScript error
    const style: CSSProperties = {
        transform: `rotateY(${angle}deg) translateZ(${translateZ}px) scale(${scale})`,
        zIndex,
        opacity: isVisible ? 1 : 0,
        pointerEvents: isVisible ? 'auto' : 'none',
        // Type assertion needed for pointerEvents
    };

    return style;
  };

  return (
    <div className="relative w-full h-screen bg-black flex justify-center items-center overflow-hidden">
      {/* Solid Black Background */}
      <div className="absolute inset-0 bg-black" />

      {/* Carousel Container */}
      <div
        className="relative w-[900px] h-[400px] flex items-center justify-center"
        style={{ perspective: '1500px', transformStyle: 'preserve-3d' }} 
      >
        {posters.map((poster, index) => (
          <div
            key={poster.id}
            className="absolute transition-all duration-700 ease-in-out cursor-pointer"
            // The fix is applied here, casting the returned object
            style={getTransform(index) as CSSProperties}
            onClick={() => goToSlide(index)}
          >
            <div className="w-50 h-76 relative bg-gray-900 rounded-lg overflow-hidden border border-white/20">
              <Image
                src={poster.src}
                alt={poster.alt}
                fill
                className="object-cover"
              />
            </div>
          </div>
        ))}
      </div>

      {/* Navigation Arrows */}
      <div className="absolute bottom-16 flex justify-center space-x-20">
        <button
          onClick={prevSlide}
          className="w-16 h-16 bg-white/10 backdrop-blur-md border border-white/30 rounded-full flex items-center justify-center text-white hover:bg-white/20 transition-all duration-300 group"
        >
          <svg className="w-8 h-8 group-hover:scale-110 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
        </button>
        <button
          onClick={nextSlide}
          className="w-16 h-16 bg-white/10 backdrop-blur-md border border-white/30 rounded-full flex items-center justify-center text-white hover:bg-white/20 transition-all duration-300 group"
        >
          <svg className="w-8 h-8 group-hover:scale-110 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>
    </div>
  );
};