'use client';

import React, { useState, useRef, useEffect } from 'react';
import Image from 'next/image';

const posters = [
  { id: 1, src: '/images/poster1.jpg', alt: 'Poster 1' },
  { id: 2, src: '/images/poster2.jpg', alt: 'Poster 2' },
  { id: 3, src: '/images/poster3.jpg', alt: 'Poster 3' },
  { id: 4, src: '/images/poster4.jpg', alt: 'Poster 4' },
  { id: 5, src: '/images/poster5.jpg', alt: 'Poster 5' },
  { id: 6, src: '/images/poster6.jpg', alt: 'Poster 6' },
  { id: 7, src: '/images/poster7.jpg', alt: 'Poster 7' },
  { id: 8, src: '/images/poster8.jpg', alt: 'Poster 8' },
  { id: 9, src: '/images/poster9.jpg', alt: 'Poster 9' },
  { id: 10, src: '/images/poster10.jpg', alt: 'Poster 10' },
  { id: 11, src: '/images/poster11.jpg', alt: 'Poster 11' },
  { id: 12, src: '/images/poster12.jpg', alt: 'Poster 12' },
  { id: 13, src: '/images/poster13.jpg', alt: 'Poster 13' },
  { id: 14, src: '/images/poster14.jpg', alt: 'Poster 14' },
  { id: 15, src: '/images/poster15.jpg', alt: 'Poster 15' },
];

export default function WhiteBackgroundCarousel() {
  const [currentIndex, setCurrentIndex] = useState(0);
  const carouselRef = useRef(null);

  const visiblePostersInArc = 7;
  const halfVisible = Math.floor(visiblePostersInArc / 2);

  const nextSlide = () => setCurrentIndex((prev) => (prev + 1) % posters.length);
  const prevSlide = () =>
    setCurrentIndex((prev) => (prev - 1 + posters.length) % posters.length);

  // Swipe / drag handling
  useEffect(() => {
    let startX = 0;

    const handlePointerDown = (e) => {
      startX = e.clientX || e.touches?.[0].clientX;
    };

    const handlePointerUp = (e) => {
      const endX = e.clientX || e.changedTouches?.[0].clientX;
      const diff = endX - startX;
      if (diff < -50) nextSlide();
      else if (diff > 50) prevSlide();
    };

    const carousel = carouselRef.current;
    if (!carousel) return;

    carousel.addEventListener('mousedown', handlePointerDown);
    carousel.addEventListener('mouseup', handlePointerUp);
    carousel.addEventListener('touchstart', handlePointerDown);
    carousel.addEventListener('touchend', handlePointerUp);

    return () => {
      carousel.removeEventListener('mousedown', handlePointerDown);
      carousel.removeEventListener('mouseup', handlePointerUp);
      carousel.removeEventListener('touchstart', handlePointerDown);
      carousel.removeEventListener('touchend', handlePointerUp);
    };
  }, [currentIndex]);

  const getTransform = (index) => {
    let diff = index - currentIndex;
    if (diff > posters.length / 2) diff -= posters.length;
    if (diff < -posters.length / 2) diff += posters.length;

    const angle = (180 / (visiblePostersInArc - 1)) * diff;
    const scale = Math.max(0.6, 1 - 0.15 * Math.abs(diff));
    const translateZ = 500 + 50 * (halfVisible - Math.abs(diff));
    const zIndex = 10 - Math.abs(diff);
    const isVisible = Math.abs(diff) <= halfVisible;

    return {
      transform: `rotateY(${angle}deg) translateZ(${translateZ}px) scale(${scale})`,
      zIndex,
      opacity: isVisible ? 1 : 0,
      pointerEvents: isVisible ? 'auto' : 'none',
      transition: 'transform 0.7s ease, opacity 0.7s ease',
      position: 'absolute',
    };
  };

  return (
    <div
      ref={carouselRef}
      className="relative w-full h-screen bg-black flex justify-center items-center overflow-hidden"
      style={{ touchAction: 'pan-y' }}
    >
      <div className="absolute inset-0 bg-black" />

      <div
        className="relative w-[1000px] h-[400px] flex items-center justify-center"
        style={{ perspective: '1500px', transformStyle: 'preserve-3d' }}
      >
        {posters.map((poster, index) => (
          <div key={poster.id} style={getTransform(index)}>
            <div className="w-52 h-80 relative bg-gray-900 rounded-lg overflow-hidden border border-white/20">
              <Image src={poster.src} alt={poster.alt} fill className="object-cover" />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
